<template>
  <div class="statement-wrap">
    <div>
        <h3>隐私说明</h3>
    </div>
    <div class="statement-content">
      <div v-for = 'content in contents' class="statement-content-item">
        <div class="statement-content-title">
          {{content.title}}
        </div>
        <div class="statement-content-text">
          <p>{{content.text}}</p>
          <div v-if = content.details>
            <p v-for = 'detail in content.details'>
              {{detail.text}}
            </p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Statement',
  data () {
    return {
      contents: [{
        title: '隐私政策',
        text: '龙猫数据尊重并保护您通过我们的平台服务（包括平台、产品服务、解决方案、数据合作等）向我们提供的所有隐私。本隐私政策（“隐私政策”）适用于通过本平台服务收集的所有个人信息，描述了我们收集、使用、披露、保护信息的方式，并为您提供访问我们所维护的关于您的个人信息的渠道。本隐私政策并不适用于您通过其他方式向我们提供的或者我们通过其他公共来源所收集的属于公开信息的个人信息。'
      }, {
        title: '个人信息的收集',
        text: '我们接收并存储您在本平台提供的个人信息。我们在您使用我们的产品和服务，访问并使用本服务，以及在我们的网站上进行其他事务或活动时收集此类信息。'
      }, {
        title: '其他信息的收集',
        text: '当您访问我们的网站时，我们的网站可能会收集其他关于您的计算机的非个人信息（如您的IP地址，网页浏览器软件型号，和网上活动）。如果我们将非个人信息与从您处收到的个人信息结合使用，则在结合使用期间，该等信息将被视为个人信息。'
      }, {
        title: '个人信息使用说明',
        text: '我们可能会在以下几个与业务相关的方面使用您在本平台提供的个人信息，包括：',

        details: [{
          text: '（1）为您提供您所需要的产品和服务时；'
        }, {
          text: '（2）管理您的帐户，包括产品和服务时；'
        }, {
          text: '（3） 处理和回复您的问题和意见时；'
        }, {
          text: '（4）为您提供您可能感兴趣的选定的产品和服务的信息时；'
        }, {
          text: '（5）为您提供您可能感兴趣的选定的产品和服务的信息时；'
        }, {
          text: '（6）征求您的信息和反馈时，包括通过调研和问卷调查等方式；执行业务分析，或者是关于改善我们的产品质量、网站以及提供的服务等的其他用途。'
        }]
      }, {
        title: '个人信息保护',
        text: '我们已采取了符合惯例的安全措施以保护您提供给我们的个人信息。在提供服务过程中，我们郑重承诺，您直接提交给我们的任何非公开信息或数据都为您所私有，我们不会向任何第三方披露该等信息或将该等信息用于任何第三方。我们的任何应用中收集、提交的数据也只会用于该应用的自身用途，而不会为其他任何应用所访问和使用。'
      }, {
        title: '隐私政策的变更',
        text: '我们未来可能会更新本隐私政策。我们会在平台及时更新有关本隐私政策的重大改变通知，或在我们的网站上放置醒目的通知。'
      }]
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
 h1, h2, h3, h4, h5, h6, p{
  -webkit-margin-before: 0em;
  -webkit-margin-after: 0em;
  -webkit-margin-start: 0px;
  -webkit-margin-end: 0px;
}
.statement-wrap h3{
  width: 278px;
  margin: 20px auto 0; 
  line-height: 25px;
  text-align: center;
  font-size: 18px;
  color: #212121;
  padding-bottom: 15px;
  border-bottom: 1px solid #ebebeb;

}
.statement-content-item{
  width: 336px;
  margin: 0 auto 20px;
  box-sizing: border-box;
  border: 1px solid #ebebeb;
}
.statement-content-title {
  height: 50px;
  line-height: 50px;
  text-indent: 35px;
  color: rgb(21, 137, 254);
  background-image: url('http://p02hchp62.bkt.clouddn.com/mobile/images/statementicon@3x.png');
  background-repeat: no-repeat;
  background-size: 22px 22px;
  background-position: 5px 16px;
  padding-top: 3px;
  padding-bottom: 3px;
  border-bottom: 1px dashed #ebebeb;
}
.statement-content-text p{
  width: 290px;
  margin: 10px auto;
  font-size: 13px;
  line-height: 18px;
  color: #656565;
}
.statement-content-item:last-child{
  margin-bottom: 40px;
}
</style>



