<template>
  <div class="about-wrap">
    <div class="about-banner">
        <img v-bind:src= bar.imgurl />
    </div>
    <div class="about-content">
      <div v-for = 'content in contents' class="about-content-item">
        <div class="about-content-title">
          {{content.title}}
        </div>
        <div class="about-content-text">
          <p>{{content.text}}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'About',
  data () {
    return {
      bar: {
        imgurl: 'http://p02hchp62.bkt.clouddn.com/mobile/images/aboutbanner@3x.png'
      },
      contents: [{
        title: '北京安捷智合科技有限公司',
        text: '北京安捷智合科技有限公司成立于2014年，是一家专业的人工智能数据服务提供商。致力于人工智能大数据采集、数据标注、数据提取、数据校验、数据清洗、线上众包等服务。'
      }, {
        title: '线上众包平台 -- 龙猫众包',
        text: '线上众包平台 -- 龙猫众包 拥有百万级以上用户,提供实时在线大数据采集标注处理、问卷调查、舆情监测、营销推广、渠道检查等服务。专业致力于为人工智能企业，搜索平台，电商平台，新闻舆情等提供定制化的数据采集、数据标注等服务。'
      }, {
        title: '数据采集',
        text: '数据采集：按具体需求，对各类规定文本，各类指定图片，各种环境下的语音、视频进行采集；采集过程中可实现对其内容的筛选、文本化等相关任务。可承载千万级别以上样本的收集，单日完成10万+样本采集。'
      }, {
        title: '数据标注',
        text: '数据标注 ：通过对图像、文本、音频、视频等信息进行搜集、评估、归类，最终完成标注；标注过程中可实现对其内容进行关键词等内容的提取、清洗、脱敏、校验等相关任务。'
      }]
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
 h1, h2, h3, h4, h5, h6, p{
  -webkit-margin-before: 0em;
  -webkit-margin-after: 0em;
  -webkit-margin-start: 0px;
  -webkit-margin-end: 0px;
}
.about-banner img{
  width: 100%;
  height: 170px;
}
@media screen and (min-width: 400px){
  .about-banner img{
    height: 200px;
  }
}
@media screen and (min-width: 700px){
  .about-banner img{
    height: 400px;
  }
}
@media screen and (min-width: 1000px){
  .about-banner img{
    height: 600px;
  }
}
.about-wrap h3{
  width: 278px;
  margin: 20px auto 0; 
  line-height: 25px;
  text-align: center;
  font-size: 18px;
  color: #212121;
  padding-bottom: 15px;
  border-bottom: 1px solid #ebebeb;

}
.about-content-item{
  width: 336px;
  margin: 0 auto 20px;
  box-sizing: border-box;
  border: 1px solid #ebebeb;
}
.about-content-title {
  height: 50px;
  line-height: 50px;
  text-indent: 35px;
  color: rgb(21, 137, 254);
  background-image: url('http://p02hchp62.bkt.clouddn.com/mobile/images/abouticon@3x.png');
  background-repeat: no-repeat;
  background-size: 22px 22px;
  background-position: 5px 16px;
  padding-top: 3px;
  padding-bottom: 3px;
  border-bottom: 1px dashed #ebebeb;
}
.about-content-text p{
  width: 290px;
  margin: 10px auto;
  font-size: 13px;
  line-height: 18px;
  color: #656565;
}
.about-content-item:first-child{
  margin-top: 20px;
}
.about-content-item:last-child{
  margin-bottom: 40px;
}
</style>



