        content: '拥有专业语音采集标注转写团队，具有丰富的语言学背景并接受过专业的数据标注、转写培训， 能够为语音数据提供标注',
        image: 'http://p02hchp62.bkt.clouddn.com/mobile/images/voicecollect.png',
