        image: 'http://p02hchp62.bkt.clouddn.com/mobile/images/videocollect.png',
        button: '立即免费试用'
      }, {
        title: '文本数据采集标注',
        content: '安捷智合能够为客户提供多种类的文本语料数据采集标注服务。文本语料数据采集的内容覆盖科研、生活、工作、娱乐',
        image: 'http://p02hchp62.bkt.clouddn.com/mobile/images/textcollect.png',
        button: '立即免费试用'
      }],
      advantages: [{
        title: '速度快',
        content: '数百万真实样本用户，快速回收巡检数据。全流程质量管控策略',
        image: 'http://p02hchp62.bkt.clouddn.com/mobile/images/indexspeed.png'
      }, {
        title: '覆盖广',
        content: '众包用户覆盖全国，不同性别、年龄、学历、多语言等人群，移动终端设备超百万台',
        image: 'http://p02hchp62.bkt.clouddn.com/mobile/images/indexrange.png'
      }, {
        title: '质量高',
        content: '龙猫众包平台的高效协作和质量管控高质量的完成交付',
        image: 'http://p02hchp62.bkt.clouddn.com/mobile/images/indexquality.png'
      }, {
        title: '低成本',
        content: '龙猫众包平台的高效协作和质量管控高质量的完成交付',
        image: 'http://p02hchp62.bkt.clouddn.com/mobile/images/indexcost.png'
      }],
      companys: [{
        image: 'http://p02hchp62.bkt.clouddn.com/mobile/images/companytx.png',
        title: '腾讯'
      }, {
        image: 'http://p02hchp62.bkt.clouddn.com/mobile/images/companybd.png',
        title: '百度'
      }, {
        image: 'http://p02hchp62.bkt.clouddn.com/mobile/images/companycmww.png',
        title: '出门问问'
      }, {
        image: 'http://p02hchp62.bkt.clouddn.com/mobile/images/companyxm.png',
        title: '小米'
      }, {
        image: 'http://p02hchp62.bkt.clouddn.com/mobile/images/companylhxk.png',
        title: '猎户星空'

      }, {
        image: 'http://p02hchp62.bkt.clouddn.com/mobile/images/companyyckj.png',
