  },
  mounted: function () {
      // var APP_ID = 'Gry1S3RjPfdBXK0Lfqx8UwcC-gzGzoHsz'
      // var APP_KEY = 'dl9Oi6tRe7pxTid1s8mUO5TS'
      // AV.init({
      //   appId: APP_ID,
      //   appKey: APP_KEY
      // })
      // // 浏览器的 Console 中设置 localStorage
      // localStorage.setItem('debug', 'leancloud*')

      $('.confirm').on('click', function () {
        var Demand = AV.Object.extend('Demand');
      if($('.form-name').val() == ''){
        alert('请填写您的姓名')
        return
      }
      if( $('.form-email').val() == ''){
        alert('请填写您的邮箱')
        return
      }
      if($('.form-type').val() == '') {
        alert('请填写您的试用类型')
        return
      }
      if($('.form-phone').val() == '') {
        alert('请填写您的手机号码')
        return
      }
      if($('.form-address').val() == '') {
        alert('请填写您的地址')
        return
      }
      if($('.form-require').val() == '') {
        alert('请填写您的需求')
        return
      }
      // 姓名
      var formname = $('.form-name').val()
      // 邮箱
      var formemail = $('.form-email').val()
      // 类型
      var formtype = $('.form-type').val()
      // 电话
      var formphone = $('.form-phone').val()
      // 地址
      var formaddress = $('.form-address').val();
      // 需求
      var formrequire = $('.form-require').val();

      var demand = new Demand()
      demand.set('customName', formname)
      demand.set('customType', formtype)
      demand.set('customTel', formphone)
      demand.set('customEmail', formemail)
      demand.set('customAddress', formaddress)
      demand.set('customDemand', formrequire)
      demand.save().then(function () {
        // 重置表单
        // 姓名
        $('.form-name').val('')
        // 邮箱
        $('.form-email').val('')
        // 类型
        $('.form-type').val('')
        // 电话
        $('.form-phone').val('')
        // 地址
        $('.form-address').val('');
        // 需求
        $('.form-require').val('');

        }, function (err) {
          alert('提交失败')
        })
      })
  },
  methods: {
    scrollone: function () {
      window.scroll(0, 2400)
    }
  }
}
