<template>
  <div id="app">
    <div class='header'>
      <div class ='content'>
        <img class='logo' src='http://p02hchp62.bkt.clouddn.com/mobile/images/logo.png' alt='logo'/>
        <p class='title'>龙猫数据</p>
        <img class='icon' src='http://p02hchp62.bkt.clouddn.com/mobile/images/open.png' alt='open' v-on:click = toggle />
      </div>
    </div>
    <div class='navigation' id = 'navigation'>
      <router-link to = '/index'><div v-on:click = changeStyle>首页</div></router-link> 
      <router-link to = '/service'><div v-on:click = changeStyle>产品服务</div></router-link> 
      <router-link to = '/solutions' ><div class='link-active' v-on:click = changeStyle>行业方案</div></router-link> 
      <router-link to = '/download'><div v-on:click = changeStyle>APP下载</div></router-link>
      <router-link to = '/about'><div v-on:click = changeStyle>关于我们</div></router-link>
      <router-link to = '/agreement'><div v-on:click = changeStyle>服务协议</div></router-link>
      <router-link to = '/statement'><div v-on:click = changeStyle>隐私说明</div></router-link>
    </div>
    <!-- 路由匹配到的组件 -->
    <router-view></router-view>
    
    <!-- 侧边栏 -->
    <div class="sidebar">
      <div class="icon-wrap" id="iconwrap">
        <div v-on:click = qqEvent>
          <div><img src="http://p02hchp62.bkt.clouddn.com/mobile/images/qq.png"></div>
        </div>
        <div v-on:click = telEvent id="tel">
          <div>
            <img src="http://p02hchp62.bkt.clouddn.com/mobile/images/info.png">
          </div>
        </div>
        <div v-on:click = infoEvent>
          <div>
            <img src="http://p02hchp62.bkt.clouddn.com/mobile/images/info.png">
          </div>
        </div>
      </div>
      <div class="customerservice-wrap" id="customerservicewrap" v-on:click = customerservicewrap><img src="http://p02hchp62.bkt.clouddn.com/mobile/images/customerservice@3x.png"></div>
      <div class="returntop-wrap" id="returntop" v-on:click = returnTopEvent ><img src="http://p02hchp62.bkt.clouddn.com/mobile/images/returntop@3x.png"></div>
    </div>
  
    <!-- 表单 -->
    <div class="fixed-panel-common" id="fixed-panel-common" style="display: none;" v-on:click.prevent.sotp = fixedclickCommon v-on:touchmove.prevent.stop = fixedtouchmoveCommon>
      <div class="free-wrap-common" id='free-wrap-common'>
        <h3>免费试用</h3>
        <div class="form-wrap-common">
          <form>
            <input type="text" name="" class="form-name-common" placeholder="您的姓名">
            <input type="email" name="" class="form-email-common" placeholder="Email">
            <input type="text" name="" class="form-type-common" placeholder="试用类型">
            <input type="text" name="" class="form-phone-common" placeholder="手机号码">
            <input type="text" name="" class="form-address-common" placeholder="联系地址">
            <textarea class="form-require-common" placeholder="需求分析"></textarea>
            <div class="btn-wrap-common"><button class="btn btn-primary cancel" v-on:click = cancelCommon>取消</button><button class="btn btn-success confirm-common">发送</button></div>
          </form>
        </div>
      </div>
    </div>

    <!-- 页脚 -->
    <div class="footer">
      <h3>联系我们</h3>
      <div class="contact-type-wrap">
        <p class="business">业务咨询：+86 152-0151-6012</p>
        <p class="cooperation">渠道合作：+86 188-2080-2980</p>
        <p class="commerce">商务洽谈：hello@longmaosoft.com</p>
        <p class="marketing">市场推广：+86 134-6668-8595</p>
        <p class="customerservice">客服热线：+86 400-000-7190</p>
        <p class="onlineconsultation">在线咨询：<span class="qq-icon">""</span><span class="qq-span"><a target="_blank" href="http://wpa.b.qq.com/cgi/wpa.php?ln=1&key=XzkzODE5NjczMF80NzcxODFfNDAwMDAwNzE5MF8yXw">联系客服</a></span></p>
      </div>
      <div class="contact-address-wrap">
        <p>北京安捷智合科技有限公司</p>
        <p>地址：北京市海淀区知春路锦秋国际大厦A座709</p>
        <p>Copyright © 2017 BY Longmaosoft.com</p>
      </div>
    </div>
  </div>
</template>

<script>
import './assets/av-min.js'
export default {
  name: 'app',
  mounted: function(){
    // 返回顶部
    var returntop = $('#returntop');
    document.addEventListener('scroll', function(e){
      e.stopPropagation();
      e.preventDefault();
      var clientH=window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight;
      if(window.scrollY > clientH){
        returntop.fadeIn();
      }else{
        returntop.fadeOut();
      }
    })

    // 表单事件提交
    $('.confirm-common').on('click', function () {
      var Demand = AV.Object.extend('Demand');
      if($('.form-name-common').val() == ''){
        alert('请填写您的姓名')
        return
      }
      if( $('.form-email-common').val() == ''){
        alert('请填写您的邮箱')
        return
      }
      if($('.form-type-common').val() == '') {
        alert('请填写您的试用类型')
        return
      }
      if($('.form-phone-common').val() == '') {
        alert('请填写您的手机号码')
        return
      }
      if($('.form-address-common').val() == '') {
        alert('请填写您的地址')
        return
      }
      if($('.form-require-common').val() == '') {
        alert('请填写您的需求')
        return
      }
      // 姓名
      var formname = $('.form-name-common').val()
      // 邮箱
      var formemail = $('.form-email-common').val()
      // 类型
      var formtype = $('.form-type-common').val()
      // 电话
      var formphone = $('.form-phone-common').val()
      // 地址
      var formaddress = $('.form-address-common').val();
      // 需求
      var customDemand = $('.form-require-common').val();

      var demand = new Demand()
      demand.set('customName', formname)
      demand.set('customType', formtype)
      demand.set('customTel', formphone)
      demand.set('customEmail', formemail)
      demand.set('customAddress', formaddress)
      demand.set('customDemand', customDemand)
      demand.save().then(function () {
        // 重置表单
        // 姓名
        $('.form-name-common').val('')
        // 邮箱
        $('.form-email-common').val('')
        // 类型
        $('.form-type-common').val('')
        // 电话
        $('.form-phone-common').val('')
        // 地址
        $('.form-address-common').val('');
        // 需求
        $('.form-require-common').val('');

        var fixedpanel = document.getElementById('fixed-panel-common')
        fixedpanel.style.display = 'none'
        }, function (err) {
          alert('提交失败')
      })
    })
  },
  methods: {
    toggle: function (event) {
      var nav = document.getElementById('navigation')
      var arr = event.target.src.split('/')
      var len = arr.length
      var name = arr[len - 1]
      if (name === 'open.png') {
        arr[len - 1] = 'close.png'
        event.target.src = arr.join('/')
        nav.style.display = 'block'
      } else {
        arr[len - 1] = 'open.png'
        event.target.src = arr.join('/')
        nav.style.display = 'none'
      }
    },
    changeStyle: function (event) {
      var icon = document.getElementsByClassName('icon')[0]
      var nav = document.getElementById('navigation')
      nav.style.display = 'none'
      var arr = icon.src.split('/')
      var len = arr.length
      var name = arr[len - 1]
      if (name === 'open.png') {
        arr[len - 1] = 'close.png'
        icon.src = arr.join('/')
      } else {
        arr[len - 1] = 'open.png'
        icon.src = arr.join('/')
      }
    },
    returnTopEvent: function(e){
      document.body.scrollTop = 0
      document.documentElement.scrollTop = 0
    },
    customerservicewrap: function(e){
      var iconwrap = $('#iconwrap');
      iconwrap.toggle();
    },
    qqEvent: function(e){      
      window.open('http://wpa.b.qq.com/cgi/wpa.php?ln=1&key=XzkzODE5NjczMF80NzcxODFfNDAwMDAwNzE5MF8yXw');
      var iconwrap = $('#iconwrap');
      iconwrap.toggle();
    },
    telEvent: function(e){
      window.location.href = "tel:400-000-7190";
      var iconwrap = $('#iconwrap');
      iconwrap.toggle();
    },
    infoEvent: function(e){
      // 显示表单
      var fixedpanelCommon = document.getElementById('fixed-panel-common')
      fixedpanelCommon.style.display = 'block'

      var iconwrap = $('#iconwrap');
      iconwrap.toggle();
    },
    cancelCommon: function(){
      // 隐藏表单
      var fixedpanelCommon = document.getElementById('fixed-panel-common')
      fixedpanelCommon.style.display = 'none'

    },
    fixedclickCommon: function(){
      return false;
    },
    fixedtouchmoveCommon: function(){
      return false;
    }

  }
}
</script>

