<template>
  <div class="service-wrap">
    <div class="voice-banner">
      <img v-bind:src= bar.voiceimgurl />
    </div>
    <div class="voicecontent">
      <div v-for = 'voicecontent in voicecontents' class="voicecontent-item">
        <img v-bind:src= voicecontent.icon>
        <h3>{{voicecontent.title}}</h3>
        <p>{{voicecontent.text}}</p>
        <p v-if = voicecontent.textother class="voicecontent-item-other-p">{{voicecontent.textother}}</p>
        <button v-on:click = tryout>{{voicecontent.btntext}}</button>
      </div>
    </div>
    <div>
      <img v-bind:src= 'bar.picimgurl'>
    </div>
    <div class="piccontent">
      <div v-for = 'piccontent in piccontents' class="piccontent-item">
        <div class="piccontent-item-left">
          <img v-bind:src= piccontent.icon />
        </div>
        <div class="piccontent-item-right">
          <h3>{{piccontent.title}}</h3>
          <p>{{piccontent.text}}</p>
        </div>
      </div>
    </div>
    <div>
      <img v-bind:src= bar.textimgurl>
    </div>
    <div class="textcontent">
      <div v-for = 'textcontent in textcontents' class="textcontent-item">
        <div class="textcontent-item-left">
          <img v-bind:src= textcontent.icon />
        </div>
        <div class="textcontent-item-right">
          <h3>{{textcontent.title}}</h3>
          <p>{{textcontent.text}}</p>
        </div>
      </div>
    </div>
    <div>
      <img v-bind:src= bar.goodsimgurl>
    </div>
    <div class="goodscontent">
      <div v-for = 'goodscontent in goodscontents' class="goodscontent-item">
        <img v-bind:src= goodscontent.icon >
        <p>{{goodscontent.text}}</p>
      </div>
    </div>
    <div class = 'halving-line'>
    </div>
    <div class="fixed-panel" id="fixed-panel" style="display: none;" v-on:click.prevent.sotp = fixedclick v-on:touchmove.prevent.stop = fixedtouchmove>
      <div class="free-wrap" id='free-wrap'>
        <h3>免费试用</h3>
        <div class="form-wrap">
          <form>

            <input type="text" name="" class="form-name" placeholder="您的姓名">
            <input type="email" name="" class="form-email" placeholder="Email">
            <input type="text" name="" class="form-type" placeholder="试用类型">
            <input type="text" name="" class="form-phone" placeholder="手机号码">
            <input type="text" name="" class="form-address" placeholder="联系地址">
            <textarea class="form-require" placeholder="需求分析"></textarea>
            <div class="btn-wrap"><button class="btn btn-primary cancel" v-on:click = cancel>取消</button><button class="btn btn-success confirm">发送</button></div>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import '../assets/initLeancloud.js'
export default {
  name: 'Solutions',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      bar: {
        voiceimgurl: 'http://p02hchp62.bkt.clouddn.com/mobile/images/voicebanner@3x.png',
        picimgurl: 'http://p02hchp62.bkt.clouddn.com/mobile/images/picbanner@3x.png',
        textimgurl: 'static/textbanner@3x.png',
        goodsimgurl: 'http://p02hchp62.bkt.clouddn.com/mobile/images/goodsbanner@3x.png'
      },
      voicecontents: [{
        icon: 'http://p02hchp62.bkt.clouddn.com/mobile/images/voiceicon01@3x.png',
        title: '语音内容提取',
        text: '机器并不能精确识别出语音文件中的内容，但人工可以。我们分发任务给数万内部用户，用人工听取、记录、提交、交叉对比的方式，精确转义出语音文件中的有用信息。',
        btntext: '立即免费试用'
      }, {
        icon: 'http://p02hchp62.bkt.clouddn.com/mobile/images/voiceicon02@3x.png',
        title: '语音采集',
        text: '相较于图片素材，使用常规手法对语音素材更加难以获取，尤其是对于特殊语种或内容的采样。龙猫数据可以下派要求给输完会员，会员朗读、采集符合要求的语音信息上传，可在短时间采集大量特定信息。',
        btntext: '立即免费试用'
      }, {
        icon: 'http://p02hchp62.bkt.clouddn.com/mobile/images/voiceicon03@3x.png',
        title: '语音筛选',
        text: '拥有海量语音资料就能送给机器学习了吗？大量无用、错误、嘈杂信息只会让结果事与愿违。',
        textother: '龙猫数据可以准确对语音文件进行有效性筛选（如内容是否清晰、是否为正确的语音），或提供语音的属性筛选（如男声、女声、孩童、老人）',
        btntext: '立即免费试用'
      }],
      piccontents: [{
        icon: 'http://p02hchp62.bkt.clouddn.com/mobile/images/picicon01@3x.png',
        title: '图像归类',
        text: '快速筛选海量图片使之符合特定的归类、去掉多余干扰图片。'
      }, {
        icon: 'http://p02hchp62.bkt.clouddn.com/mobile/images/picicon02@3x.png',
        title: '图像内容标记',
        text: '对图像里包含的元素进行标记，标记出图像中的所有元素或部分元素，比如一张街道图片中的交通指示灯、行人、斑马线、各色车辆等。'
      }, {
        icon: 'http://p02hchp62.bkt.clouddn.com/mobile/images/picicon03@3x.png',
        title: '图像标签化',
        text: '对图像打标签，使海量图片可针对性管理，比如针对电商大量商品服装宣传图片，可个图打上裙子、裤子等的标签。'
      }, {
        icon: 'http://p02hchp62.bkt.clouddn.com/mobile/images/picicon04@3x.png',
        title: '图像采集',
        text: '采集大量符合要求的图像。如之前为客户采集的各银行卡片照片、不同角度人脸照片、不同年龄段全身照片。'
      }],
      textcontents: [{
        icon: 'http://p02hchp62.bkt.clouddn.com/mobile/images/texticon01@3x.png',
        title: '文本归类',
        text: '大数据时代拥有海量文本轻而易举，但从中归纳出有效信息却并不简单。龙猫众包可快速归类文本（如把海量的评价内容归类为正面或反面），让信息变有效。'
      }, {
        icon: 'http://p02hchp62.bkt.clouddn.com/mobile/images/texticon02@3x.png',
        title: '文本提取',
        text: '从网页、书籍、文章里提取出特定文本。如概括每篇新闻主要内容、每个网页的关键词。'
      }, {
        icon: 'http://p02hchp62.bkt.clouddn.com/mobile/images/texticon03@3x.png',
        title: '文本校正',
        text: '人工校正机器处理的语音，比如机器翻译、转写后的错误校正，字幕错误校正等。'

      }],
      goodscontents: [{
        icon: 'http://p02hchp62.bkt.clouddn.com/mobile/images/goodsicon01@3x.png',
        text: '花大量资源铺设的渠道，陈列的产品是否新鲜、保质期是否符合要求？龙猫数据调动遍布全国各地的用户快速检查产品新鲜度，帮助企业做产品新鲜度管理，给企业主实时反馈，以便作出市场应对策略。'
      }, {
        icon: 'http://p02hchp62.bkt.clouddn.com/mobile/images/goodsicon02@3x.png',
        text: '畅销的产品经常会出现缺货或未被足够陈列，龙猫众包帮您实时巡查店内货架情况，帮助优化畅销供应链，提升产品动销，从而提升消费者购物体验。'
      }]
    }
  },
  mounted: function () {
      $('.confirm').on('click', function () {
        var Demand = AV.Object.extend('Demand');
        if($('.form-name').val() == ''){
          alert('请填写您的姓名')
          return
        }
        if( $('.form-email').val() == ''){
          alert('请填写您的邮箱')
          return
        }
        if($('.form-type').val() == '') {
          alert('请填写您的试用类型')
          return
        }
        if($('.form-phone').val() == '') {
          alert('请填写您的手机号码')
          return
        }
        if($('.form-address').val() == '') {
          alert('请填写您的地址')
          return
        }
        if($('.form-require').val() == '') {
          alert('请填写您的需求')
          return
        }
        // 姓名
        var formname = $('.form-name').val()
        // 邮箱
        var formemail = $('.form-email').val()
        // 类型
        var formtype = $('.form-type').val()
        // 电话
        var formphone = $('.form-phone').val()
        // 地址
        var formaddress = $('.form-address').val();
        // 需求
        var customDemand = $('.form-require').val();

        var demand = new Demand()
        demand.set('customName', formname)
        demand.set('customType', formtype)
        demand.set('customTel', formphone)
        demand.set('customEmail', formemail)
        demand.set('customAddress', formaddress)
        demand.set('customDemand', customDemand)
        demand.save().then(function () {
          // 重置表单
          // 姓名
          $('.form-name').val('')
          // 邮箱
          $('.form-email').val('')
          // 类型
          $('.form-type').val('')
          // 电话
          $('.form-phone').val('')
          // 地址
          $('.form-address').val('');
          // 需求
          $('.form-require').val('');

          var fixedpanel = document.getElementById('fixed-panel')
          fixedpanel.style.display = 'none'

          }, function (err) {
            alert('提交失败')
          })
        })
 
  },
  methods: {
    tryout: function () {
      var fixedpanel = document.getElementById('fixed-panel')
      fixedpanel.style.display = 'block'
    },
    cancel: function () {
      var fixedpanel = document.getElementById('fixed-panel')
      fixedpanel.style.display = 'none'
    },
    fixedclick: function(){
      return false;
    },
    fixedtouchmove: function(){
      return false;
    }

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
div,img{
  padding: 0;
  margin: 0;
}
.service-wrap{
  position: relative;
}
.service-wrap img{
  width: 100%;
  height: 170px;
}
@media screen and (min-width: 400px){
  .service-wrap img{
    width: 100%;
    height: 200px;
  }
}
@media screen and (min-width: 600px){
  .service-wrap img{
    width: 100%;
    height: 400px;
  }
}
@media screen and (min-width: 1000px){
  .service-wrap img{
    width: 100%;
    height: 600px;
  }
}
