.voicecontent{
  width: 273px;
  text-align: center;
  margin: 0 auto;
}
.voicecontent-item{
  margin-top: 20px;
  border-bottom: 1px solid #ebebeb;
}
.voicecontent-item:last-child{
  border-bottom: none;
}
.voicecontent img{
  height: 152px;
}
.voicecontent h3{
  margin: 10px 0;
  line-height: 22px;
  font-size: 16px;
  color: #212121;
}
.voicecontent p{
  line-height: 17px;
  font-size: 12px;
  color: #666;
}
.voicecontent-item-other-p{
  margin-top: 10px;
}
.voicecontent button{
  width: 112px;
  height: 26px;
  margin: 15px 0 20px;
  line-height: 20px;
  font-size: 12px;
  color: #fff;
  border-radius: 13px;
  background-color: #0197ff;
}
/*图像处理*/
.piccontent{
  width: 290px;
  margin: 0 auto;
}
.piccontent-item{
  position: relative;
  height: 130px;
  border-bottom: 1px solid #d4d4d4;
}
.piccontent-item:last-child{
  border-bottom: none; 
}
.piccontent-item-left{
  position: absolute;
  top: 32px;
  left: 0;
  width: 64px;
  height: 64px;
}
.piccontent-item-left img{
  width: 64px;
  height: 64px;
}
.piccontent-item-right{
  position: absolute;
  top: 10px;
  left: 79px;
  width: 211px;
}
.piccontent-item h3{
  margin: 10px 0;
  font-size: 16px;
  line-height: 25px;
  color: #333;
}
.piccontent-item p{
  font-size: 12px;
  color: #666666;
  line-height: 17px;

}
/* 文本处理*/
.textcontent{
  width: 290px;
  margin: 0 auto;
}
.textcontent-item{
  position: relative;
  height: 130px;
  border-bottom: 1px solid #d4d4d4;
}
.textcontent-item:last-child{
  border-bottom: none; 
}
.textcontent-item:first-child{
  height: 160px;
}
.textcontent-item-left{
  position: absolute;
  top: 32px;
  left: 0;
  width: 64px;
  height: 64px;
}
.textcontent-item:first-child .textcontent-item-left{
  top: 50px;
}
.textcontent-item-left img{
  width: 64px;
  height: 64px;
}
.textcontent-item-right{
  position: absolute;
  top: 10px;
  left: 79px;
  width: 211px;
}
.textcontent-item h3{
  margin: 10px 0;
  font-size: 16px;
  line-height: 25px;
  color: #333;
}
.textcontent-item p{
  font-size: 12px;
  color: #666666;
  line-height: 17px;
}
/*快消品*/
.goodscontent{
  width: 274px;
  margin: 0 auto;
}
.goodscontent-item{
  margin: 20px 0;
}
.goodscontent-item:first-child{
  padding-bottom: 10px;
  border-bottom: 1px solid #ebebeb;
}
.goodscontent img{
  width: 100%;
  height: 134px;
  margin-bottom: 20px;
}
.goodscontent p{
  font-size: 15px;
  color: #666;
  line-height: 21px;
}
/*分割线*/
