  top: 50%;
  left: 50%;

  transform: translate(-160px, -240px);
  border-top: 10px solid #ebebeb;
  background-color: #fff;
  border-radius: 5px;
}
.free-wrap h3{
  text-align: center;
  line-height: 22px;
  font-size: 16px;
}
.form-wrap{
  width: 320px;
  margin: 0 auto;

}
.form-wrap input{
  display: block;
  text-indent: 25px;
  width: 100%;
  height: 39px;
  margin-bottom: 10px;
  background-repeat: no-repeat;
  box-sizing: border-box;
  outline: none;
