.form-name{
  background-image: url('http://p02hchp62.bkt.clouddn.com/mobile/images/formname.png');
  background-position: 5px 14px;
}
.form-email{
  background-image: url('http://p02hchp62.bkt.clouddn.com/mobile/images/formemail.png');
