  background-image: url('http://p02hchp62.bkt.clouddn.com/mobile/images/formrequire.png');
  background-repeat: no-repeat;
  background-position: 5px 3px;
  border: 1px solid #E9E9E9;
}
.form-wrap .form-submit{
  text-indent: 0;
  margin-top: 10px;
