<template>
  <div class="service-wrap">
    <div class="service-banner">
      <img v-bind:src= bar.imgurl>
    </div>
    <div class="search-wrap">
      <!-- <input type="text" name="search" class=""><span>搜索</span> -->
    </div>
    <div class="service-list-title"><h3>产品列表</h3></div>
    <div class="service-content-item">
      <div v-for = 'product in contexts.products'>
        <img v-bind:src= product.productImageUrl>
        <h3>{{product.productTitle}}</h3>
        <div class="tag-wrap">
          <span v-for = 'tag in product.productTags' class="tag">{{tag}}</span>
        </div>
        <p>{{product.productDescription}}</p>
      </div>
    </div>
  </div>
 
</template>

<script>
export default {
  name: 'Solutions',
  data () {
    return {
      bar: {
        imgurl: 'http://p02hchp62.bkt.clouddn.com/mobile/images/serverbanner.png'
      },
      contexts: {

      }
    }
  },
  mounted: function(){
      var context = {
        products: []
      }
      // LeanCloud - 查询
      var query = new AV.Query('Product');
      query.include('owner');
      query.include('image');
      query.ascending('num_id');
      query.addDescending('createdAt');
      query.find().then(function (products) {
         products.forEach(function(product) {

            // console.log(product.get('title'))

            // 产品名称 
            var productTitle = product.get('title');
            // 产品标签
            var productTags = product.get('tags');
            // 产品描述
            var productDescription = product.get('description');
            // 时间
            var releaseTime = (product.createdAt.getMonth() + 1) + '/' + product.createdAt.getDate() + '/' +  product.createdAt.getFullYear();
            // 发布者
            var ownerUsername = product.get('owner').get('username');
            // 产品图片描述
            var productImage = product.get('image');
            var productImageUrl;
            if (productImage) {
              productImageUrl = productImage.get('url');
            } else {
              productImageUrl = 'favicon.ico';
            }
            // 产品详情
            var details = product.get('details');
            // 详情图片个数
            var detailsImgArrLength = product.get('detailsImgArrLength');
            // 获取图片详情
            var detailsImgArr = [];
            var detailsImgArrUrl = [];

            for(var i = 0; i < detailsImgArrLength; i++){
              detailsImgArr.push(product.get('detailsImgArr' + i));
              detailsImgArrUrl.push(product.get('detailsImgArr' + i).get('url'));
            }

            // 产品详情图片描述
            var detailsTextArr = product.get('detailsTextArr');

            var detailsAll = []
            for(var i = 0; i < detailsImgArrUrl.length; i++){
              var obj = {};
              obj.detailsImgArrUrl = detailsImgArrUrl[i];
              obj.detailsTextArr = detailsTextArr[i];
              detailsAll.push(obj);
            }

            // 数据样例图片个数
            var exampleImgArrLength =product.get('exampleImgArrLength');
            var exampleImgArr = [];
            var exampleImageUrl = [];
            for(var i = 0; i < exampleImgArrLength; i++){
              exampleImgArr.push(product.get('exampleImgArr' + i));
              exampleImageUrl.push(product.get('exampleImgArr' + i).get('url'))
            }
            // 数据样例图片描述
            var exampleTextArr = product.get('exampleTextArr');  
            var exampleAll = []
            for(var i = 0; i < exampleImageUrl.length; i++){
              var obj = {}
              obj.exampleImageUrl = exampleImageUrl[i];
              obj.exampleTextArr = exampleTextArr[i];
              exampleAll.push(obj);
            }
            
            context.products.push({
              // 产品图片
              productImageUrl,
              // 产品名称
              productTitle,
              // 产品标签
              productTags,
              // 产品描述
              productDescription,
              // 所有者
              ownerUsername,
              // 提交时间
              releaseTime
            })
         });
      }).catch(function(error) {
        // console.log(error)
        alert(JSON.stringify(error));
      });
      this.contexts = context;
      console.log(this.contexts)
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.service-banner{
  position: relative;
}
.service-banner img{
  width: 100%;
  height: 170px;
}
@media screen and (min-width: 400px){
  .service-banner img{
    width: 100%;
    height: 200px;
  }
}
@media screen and (min-width: 600px){
  .service-banner img{
    width: 100%;
    height: 400px;
  }
}
@media screen and (min-width: 1000px){
  .service-banner img{
    width: 100%;
    height: 600px;
  }
}
.search-wrap{
  position: relative;
  width: 273px;
  margin: 10px auto;
}
.search-wrap input{
  display: inline-block;
  width: 273px;
  height: 37px;
  line-height: 37px;
  font-size: 14px;
  text-indent: 35px;
  outline: none;
  padding-right: 50px;
  border: 1px solid #FF5300;
  background-image: url('http://p02hchp62.bkt.clouddn.com/mobile/images/searchicon@2x.png');
  background-repeat: no-repeat;
  background-size: 19px 19px;
  background-position: 10px;
}
.search-wrap span{
  position: absolute;
  display: inline-block;
  top: 10px;
  left: 220px;
  font-size: 14px;
  line-height: 16px;
  height: 16px;
  padding-left: 10px;
  border-left: 2px solid #979797;
  color: #ff5300;
}
.service-list-title h3{
    font-size: 18px;
    line-height: 25px;
    margin: 20px 0;
    color: #666666;
    text-align: center;
}
.service-content-item{
  width: 273px;
  min-height: 800px;
  margin: 20px auto;
}
.service-content-item img{
  width: 100%;
  height: 165px;
  margin-bottom: 10px;
}
.service-content-item h3{
  line-height: 22px;
  font-size: 16px;
  text-align: center;
  color: #212121;
}
.service-content-item .tag-wrap{
  margin: 10px 0;
  text-align: center;
}
.service-content-item .tag{
  display: inline-block;
  margin: 0 10px 5px;
  padding: 2px 6px;
  border: 1px solid #eee;
}
.service-content-item p{  
  font-size: 12px;
  color: #666;
  line-height: 17px;
  text-align: justify;
  text-justify:inter-ideograph;
  margin-bottom: 30px;
}


</style>



