import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/Index'
import Service from '@/components/Service'
import Solutions from '@/components/Solutions'
import Download from '@/components/Download'
import About from '@/components/About'
import Agreement from '@/components/Agreement'
import Statement from '@/components/Statement'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: '/',
      component: Index
    },
    {
      path: '/index',
      name: 'Index',
      component: Index
    },
    {
      path: '/service',
      name: 'Service',
      component: Service
    },
    {
      path: '/solutions',
      name: 'Solutions',
      component: Solutions
    },
    {
      path: '/download',
      name: 'Download',
      component: Download
    },
    {
      path: '/about',
      name: 'About',
      component: About
    },
    {
      path: '/agreement',
      name: 'Agreement',
      component: Agreement
    },
    {
      path: '/statement',
      name: 'Statement',
      component: Statement
    }
  ]
})



